<template>
    <div>
        <div class="container">
            <div class="row">
                <div class="col-md-8">
                    <figure>
                        <img :src="news.image" alt="">
                    </figure>
                    <h1 class="title">{{news.title}}</h1>
                    <h5>{{news.resume}}</h5>
                    <div class="content" v-html="news.text"></div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

    import newsMixis from '../../../mixins/news'

    export default {
        mixins: [newsMixis],
        components: {
        },
        mounted() {
            this.preload(true);
            this.getNews(this.$route.params.id).then(() => this.preload(false));
        },
        methods: {}

    }
</script>
<style>
    .content-box {
        padding: 0;
    }
